import React, { Component } from "react";
import Sheet from "../images/sheets.png";

class goog extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <React.Fragment>
        <div style={{ marginLeft: "250px" }}>
          <div>Sign in Page</div>
          <a
            href="https://stravasync.pythonanywhere.com/url"
            rel="noopener noreferrer"
            target="_blank"
          >
            <img alt="Google" src={Sheet} height="60px"/>
          </a>
        </div>
      </React.Fragment>
    );
  }
}

export default goog;
