import React, { Component } from "react";
import "../style.css";

class Sheet extends Component {
  constructor(props) {
    super(props);
    this.state = {
        update: false,
    };
    this.UpdateOnClick = () => {
        if (this.state.update) {
            this.setState({ update: false });
        } else {
            this.setState({ update: true });
            this.props.onChangeId()
        }
    };
    this.UnUpdateOnClick = () => {
        this.setState({ update: false });
    };
  }
  render() {
    let { title, id, onChangeId } = this.props;
        return (
            <div className="mb-2 mt-2 mr-5 project-container col-md-12">
                <div className="card" id={id} >
                    <button
                        className={ !this.props.active ? 'btn btn-info' : 'btn btn-warning' }
                        onClick={onChangeId}
                        type="button"
                        style={{ Color: "#FFF" }}
                        value={id}
                        // onClick={onChangeId}
                    >
                    <div className="card-body">
                    <h4 className="card-title">{title}</h4>
                    {/* <div className="mb-3 card-text">
                        <div>{id}</div>
                    </div> */}
                    </div>
                    </button>
                </div>
            </div>
        );
    }
}

export default Sheet;
